.site-list__item a {
  display: block;
  .font-base(@fontBold;18px;40px;700);
  text-decoration: none;
  .color(@white);
  .bg-color(@blue);
  text-align: center;
  margin: 0 auto;
  padding: 7px 0 7px 0;
  border-bottom: 1px solid @dark-blue;
  border-top: 1px solid @light-blue;
  .blue-action();
  position: relative;
}

.site-list__item:last-child {
  box-shadow: inset 0 -4px 2px 0 #76a4c1;
}

.site-list__item:first-child {
  border-top: none;
}

.site-list__icon--cross {
  fill: #ffffff;
  position: absolute;
  top: 13px;
  right: 20px;
  height: 22px;
  width: 23px;
}

@media (min-width: @tablet-width) {
  .site-list {
    .mg-auto();
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 768px;
  }

  .site-list__item a,
  .site-list__item {
    .color(@black);
    .bg-color(@white);
    border: none;
    box-shadow: none;
  }

  .site-list__item a {
    .font-base(@fontBold;14px;26px;700);
    .black-action();
  }

  .site-list__item--active a {
    .black-action--active();
  }

  .site-list__item {
    min-width: 140px;
    padding: 0 35px;

    &:last-child {
      box-shadow: none;
      padding-right: 28px;
    }

    &:nth-child(2),
    &:nth-child(3) {
      /*       -ms-flex-positive: 2;*/
      flex-grow: 2;
    }

    &:nth-child(2) {
      margin-left: -70px;
    }

    &:nth-child(3) {
      margin-right: -94px;
    }

    &:nth-child(1) {
      margin-left: 8px;
    }
  }
}

@media (min-width: @desktop-width) {
  .site-list {
    width: 1200px;

    & a {
      width: 130px;
      padding: 7px 10px;
    }

    &__item:nth-child(1) {
      margin-left: 20px;
    }

    &__item:nth-child(2) {
      margin-left: -100px;
    }

    &__item:nth-child(3) {
      margin-right: -100px;
    }

    &__item:nth-child(4) {
      margin-right: 28px;
    }
  }
}
